import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import Vuetify from 'vuetify';
import * as firebase from 'firebase'
import 'vuetify/dist/vuetify.min.css';


Vue.use(Vuetify);

Vue.config.productionTip = false;

new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>',
    created () {
        firebase.initializeApp({
          apiKey: "AIzaSyDH6TX3aNTvu9qEPpaOqLqdTg8lHzouskQ",
          authDomain: "app-commands.firebaseapp.com",
          databaseURL: "https://app-commands.firebaseio.com",
          projectId: "app-commands",
          storageBucket: "",
          messagingSenderId: "732797851970",
          appId: "1:732797851970:web:6de937449cf4b0db"
        })

        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                this.$store.dispatch('autoLoginUser', user)
            }
        })

    }
});



